*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Courier New', Courier, monospace;
  height: 100%;
}


.app{
  background-image: url('./assets/cloud.jpg');
  background-size:cover;
  background-position:bottom;
  transition: 0.4 ease;
}  

.app-warm{
  background-image: url('./assets/sun.jpeg');
  background-size:cover;
  background-position:bottom;
  transition: 0.4 ease;
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.footer{
  position:relative;
   left:0px;
   bottom:0px;
   height:42px;
   color: white;
   overflow: hidden;
   font-style: bolds;
   width:100%;
   box-shadow: 5px -8px rgba(0,0,0,0.2);
   padding-top: 10px;
   text-shadow:  rgba(50, 50, 70, 0.5);
   text-align: center;
   background-color: rgba(114, 114, 114, 0.9);
  

  
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  font-style: bold;

  background-color: rgba(255,255,255,0.8);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box  .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.5);
  border-radius: 16px;

  padding: 15px 15px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
  color:#fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}